.promises-body {
  background-image: linear-gradient(to right, #00f260, #0575e6);
  background-size: 200%;
  background-attachment: fixed;
  animation: bg-animation 5s infinite alternate;
}

@keyframes bg-animation {
  0% {
    background-position: 0;
  }

  100% {
    background-position: 100%;
  }
}

.form {
  flex-direction: column;
  gap: 50px;
  margin-left: 100px;
  font-size: 35px;
  display: flex;
}

.input {
  margin-left: 22px;
  padding: 15px;
  font-size: 25px;
}

.button {
  max-width: 200px;
  color: wheat;
  cursor: pointer;
  background-color: indigo;
  border: 0;
  border-radius: 20px;
  margin: 5px;
  padding: 15px;
  font-size: 24px;
}

.button:hover, .button:focus {
  background-color: #000;
  transform: scale(1.1);
}

.button:disabled {
  color: indigo;
}

.label {
  font-size: 35px;
  font-weight: 900;
}

